import {fireAndForget} from "@ceros/markup-common/shared/util/promise-utils";
import {RollbarModule} from "@ceros/markup-common/frontend/rollbar-client";
import {Logger} from "@ceros/markup-common/frontend/logger";
import {getPageLoadedPromise} from "@ceros/markup-common/frontend/dom-util";
import {IStashedState} from "../shared/model/stashed-state";
import {listenForProjectFrameLoad} from "./frame-load-handler";
import {listenForImportableTabs} from "./importable-tabs";
import {listenForMessages} from "./message-dispatcher";
import {requestInfo, createProject} from "./background-app";
import {initRefresher} from "./refresher";
import {restoreStateIfWasReloaded} from "./reloader";
import {injectContentScriptIntoMarkupTabs} from "./markup-content-script-injector";
import {initIconAndBadge} from "./browser-toolbar";
import {UserInfoStorage} from "../shared/storage/user-info-storage";
import {ProjectOpenStorage} from "../shared/storage/project-open-storage";
import {DeprecationStorage} from "../shared/storage/deprecation-storage";

Logger.init(RollbarModule.EXTENSION_BACKGROUND, process.env.EXTENSION_VERSION!);

chrome.runtime.onInstalled.addListener((): void => {
    // clear away any data from previous installs of extension
    fireAndForget(UserInfoStorage.setDefault(), Logger);
    fireAndForget(ProjectOpenStorage.setDefault(), Logger);
    fireAndForget(DeprecationStorage.setDefault(), Logger);
});

initIconAndBadge();
listenForMessages();
listenForProjectFrameLoad();
listenForImportableTabs();
initRefresher();
fireAndForget(injectContentScriptIntoMarkupTabs(), Logger);

fireAndForget(
    (async (): Promise<void> => {
        const [stashedState]: [IStashedState | null, void | null] = await Promise.all([restoreStateIfWasReloaded(), getPageLoadedPromise()]);
        await requestInfo();
        if (stashedState?.creatingProjectUrl) {
            await createProject(stashedState.creatingProjectUrl);
        }
    })(),
    Logger
);
