import {ErrorWithRollbarInfo} from "@ceros/markup-common/frontend/error-with-rollbar-info";

export class MarkupEmbedUnresponsiveError extends ErrorWithRollbarInfo {
    public constructor(message: string, rollbarInfo?: any) {
        super(message, rollbarInfo);
        Object.setPrototypeOf(this, MarkupEmbedUnresponsiveError.prototype);
    }
}
