import {waitForEvent} from "@ceros/markup-common/frontend/dom-util";
import {PromiseTimeoutError} from "@ceros/markup-common/shared/model/promise-timeout-error";
import {BlockingPostMessageChannel} from "@ceros/markup-common/frontend/extension/blocking-post-message-channel";
import {EXTENSION_BACKGROUND_EMBED_CHANNEL_NAME} from "@ceros/markup-common/frontend/extension/messages/extension-background-embed-channel-name";
import {
    ExtensionBackgroundEmbedMessageType,
    isInfoResponse
} from "@ceros/markup-common/frontend/extension/messages/extension-background-embed-message";
import {MessageFromExtensionBackgroundScriptType} from "@ceros/markup-common/frontend/extension/messages/extension-background-script-message";
import {PostMessageTimeoutError} from "@ceros/markup-common/frontend/extension/post-message-timeout-error";
import {asyncNoop, promiseTimeout} from "@ceros/markup-common/shared/util/promise-utils";
import {APP_ORIGIN} from "../shared/app-origin";
import {ProjectOpenStorage} from "../shared/storage/project-open-storage";
import {UserInfoStorage} from "../shared/storage/user-info-storage";
import {IframeLoadFailsStorage} from "../shared/storage/iframe-load-fails-storage";
import {ErrorWithRollbarInfo} from "@ceros/markup-common/frontend/error-with-rollbar-info";
import {NoRollbarError} from "@ceros/markup-common/frontend/no-rollbar-error";
import {MarkupEmbedUnresponsiveError} from "./errors/markup-embed-unresponsive-error";
import {reload, hasReloadedTooRecently} from "./reloader";

const IFRAME_LOAD_TIMEOUT: number = 10000;
const IFRAME_READY_MESSAGE_TIMEOUT: number = 2000;
const INFO_REQUEST_TIMEOUT: number = 10000;
const PROJECT_CREATION_TIMEOUT: number = 15000;
const LOAD_ERROR_LOG_FREQUENCY: number = 150; // send load error to rollbar (randomly) once every 150 times

export function requestInfo(): Promise<void> {
    return doInBackgroundApp(
        (messageChannel: BlockingPostMessageChannel): Promise<void> =>
            messageChannel
                .send({type: ExtensionBackgroundEmbedMessageType.INFO_REQUEST}, INFO_REQUEST_TIMEOUT)
                .catch((e: Error): void => {
                    if (e instanceof PostMessageTimeoutError) {
                        throw new Error("Extension info request timed out");
                    } else {
                        throw e;
                    }
                })
                .then(async (infoResponse: unknown): Promise<void> => {
                    if (!isInfoResponse(infoResponse)) {
                        throw new ErrorWithRollbarInfo("Extension received invalid response for info request", infoResponse);
                    }

                    await UserInfoStorage.set(infoResponse.userInfo);

                    if (infoResponse.userInfo !== null) {
                        // logged in
                        await ProjectOpenStorage.set((infoResponse as any).projectOpens);
                    }
                })
    ).catch(async (e: Error): Promise<void> => {
        if (e instanceof MarkupEmbedUnresponsiveError && !(await hasReloadedTooRecently())) {
            await reload();
            return;
        }
        throw e;
    });
}

export async function createProject(url: string): Promise<void> {
    return doInBackgroundApp(
        (messageChannel: BlockingPostMessageChannel): Promise<string> =>
            messageChannel
                .send(
                    {
                        type: ExtensionBackgroundEmbedMessageType.CREATE_PROJECT,
                        url
                    },
                    PROJECT_CREATION_TIMEOUT
                )
                .catch((e: Error): void => {
                    if (e instanceof PostMessageTimeoutError) {
                        throw new Error("Extension project creation request timed out");
                    } else {
                        throw e;
                    }
                })
                .then((response: unknown): string => {
                    if (typeof response !== "string") {
                        throw new ErrorWithRollbarInfo("Response to create project request was not a string", response);
                    }

                    return response;
                })
    )
        .then((projectUrl: string): void =>
            chrome.tabs.create({
                url: projectUrl,
                active: true
            })
        )
        .catch(async (e: Error): Promise<void> => {
            if (e instanceof MarkupEmbedUnresponsiveError && !(await hasReloadedTooRecently())) {
                await reload(url);
                return;
            }

            chrome.runtime.sendMessage({type: MessageFromExtensionBackgroundScriptType.PROJECT_CREATION_ERROR});

            throw e;
        });
}

let givenUp: boolean = false;

async function doInBackgroundApp<T>(callback: (messageChannel: BlockingPostMessageChannel) => Promise<T>): Promise<T> {
    if (givenUp) {
        throw new NoRollbarError("Extension failed to communicate with markup iframe too many consecutive times");
    }

    let iframe: HTMLIFrameElement | undefined;
    let messageChannel: BlockingPostMessageChannel | undefined;

    try {
        iframe = await loadIframe();
        messageChannel = await initializeMessaging(iframe);

        return await callback(messageChannel);
    } catch (e) {
        if (e instanceof MarkupEmbedUnresponsiveError) {
            if (consecutiveReadyEventFails >= MAX_CONSECUTIVE_FAILS) {
                givenUp = true;
                throw new MarkupEmbedUnresponsiveError(`Extension did not receive READY over ${MAX_CONSECUTIVE_FAILS} times in a row`);
            }
        }
        throw e;
    } finally {
        if (messageChannel) {
            messageChannel.destroy();
        }
        if (iframe) {
            iframe.remove();
        }
    }
}

// expose some functions on window that will allow us to test various failure conditions

// for "iframe load timeout" failure
let forceLoadFailure: boolean = false;
(window as any).setForceLoadFailure = (value: boolean): void => {
    forceLoadFailure = value;
};

// force "did not receive ready event" failure
let forceCommunicationFailure: boolean = false;
(window as any).setForceCommunicationFailure = (value: boolean): void => {
    forceCommunicationFailure = value;
};

// force "info/project creation request timeout" failure
let forceResponseFailure: boolean = false;
(window as any).setForceResponseFailure = (value: boolean): void => {
    forceResponseFailure = value;
};

// override load error log frequency
let loadErrorLogFrequencyOverride: number | undefined;
(window as any).setLoadErrorLogFrequencyOverride = (value: number | undefined): void => {
    loadErrorLogFrequencyOverride = value;
};

// We don't know why we get so many errors while trying to load the iframe.  It's possible that it's just connection errors
// and the rollbar SDK queues the error events and sends them when the connection is reestabilshed.
//
// We're trying to collect the number of _consecutive_ errors as well as the number of errors in the last 24 hour period.
// Note: we're only logging one out of 150 errors in order to not spam rollbar.
//
let consecutiveLoadTimeouts: number = 0;
async function loadIframe(): Promise<HTMLIFrameElement> {
    const wasOnline: boolean = window.navigator.onLine;

    try {
        if (forceLoadFailure) {
            throw new PromiseTimeoutError("Simulating an iframe load timeout");
        }

        const iframe: HTMLIFrameElement = window.document.createElement("iframe");
        const queryString: string = forceCommunicationFailure
            ? "?forceCommunicationFailure=1"
            : forceResponseFailure
            ? "?forceResponseFailure=1"
            : "";
        iframe.src = `${APP_ORIGIN}/extension-background.html${queryString}`;
        window.document.body.appendChild(iframe);

        await promiseTimeout(waitForEvent(iframe, "load"), IFRAME_LOAD_TIMEOUT);
        consecutiveLoadTimeouts = 0;
        return iframe;
    } catch (e) {
        if (e instanceof PromiseTimeoutError) {
            consecutiveLoadTimeouts++;
            const failsOver24Hours: number = await IframeLoadFailsStorage.add();

            if (!Math.floor(Math.random() * (loadErrorLogFrequencyOverride ?? LOAD_ERROR_LOG_FREQUENCY))) {
                throw new ErrorWithRollbarInfo("Extension failed to load markup iframe", {
                    wasOnline,
                    consecutiveFails: consecutiveLoadTimeouts,
                    failsOver24Hours
                });
            } else {
                throw new NoRollbarError("Extension failed to load markup iframe");
            }
        } else {
            throw e;
        }
    }
}

// Failure to receive the READY message from the embedded markup is something that seems to happen if users keep the
// browser open too long.  Reloading the extension when this happens mostly works but just in case, we stop trying to connect
// if post-message communication failure happens too many times in a row.
const MAX_CONSECUTIVE_FAILS: number = 3;
let consecutiveReadyEventFails: number = 0;
async function initializeMessaging(iframe: HTMLIFrameElement): Promise<BlockingPostMessageChannel> {
    const messageChannel: BlockingPostMessageChannel = new BlockingPostMessageChannel(
        asyncNoop, // no persistent listeners
        EXTENSION_BACKGROUND_EMBED_CHANNEL_NAME,
        iframe.contentWindow!,
        APP_ORIGIN
    );

    try {
        await messageChannel.waitForMessage(
            (message: any): boolean => message.type === ExtensionBackgroundEmbedMessageType.READY,
            IFRAME_READY_MESSAGE_TIMEOUT
        );
        consecutiveReadyEventFails = 0;

        return messageChannel;
    } catch (e) {
        if (e instanceof PostMessageTimeoutError) {
            consecutiveReadyEventFails++;
            throw new MarkupEmbedUnresponsiveError("Extension did not receive READY message from markup iframe", {
                consecutiveFails: consecutiveReadyEventFails
            });
        } else {
            throw e;
        }
    }
}
