import {BrowserExtensionUrlService} from "@ceros/markup-common/frontend/extension/extension-project-iframe-url-service";
import {MessageFromExtensionBackgroundScriptType} from "@ceros/markup-common/frontend/extension/messages/extension-background-script-message";
import {ProjectFrameService} from "../project-frame-service";
import {ProjectCookieStoreService} from "../project-cookie-stores-service";
import {onBeforeRequestHandler} from "./request-handler";
import {onBeforeSendHeadersHandler, onHeadersReceivedHandler} from "./headers-handlers";
import {sendTabMessage} from "../../shared/chrome-api-promises";

const urlService: BrowserExtensionUrlService = new BrowserExtensionUrlService(chrome.runtime.id);

export function listenForProjectFrameLoad(): void {
    // If a request has our param indicating that the extension should do its magic on it, we record the tab/frame id and strip the url
    chrome.webRequest.onBeforeRequest.addListener(onBeforeRequestHandler, {urls: ["<all_urls>"]}, ["blocking"]);

    // ...add request headers identifing ourselves
    chrome.webRequest.onBeforeSendHeaders.addListener(onBeforeSendHeadersHandler, {urls: ["<all_urls>"]}, [
        "blocking",
        "requestHeaders",
        "extraHeaders"
    ]);

    // ...next strip the headers
    chrome.webRequest.onHeadersReceived.addListener(onHeadersReceivedHandler, {urls: ["<all_urls>"]}, [
        "blocking",
        "responseHeaders",
        "extraHeaders"
    ]);

    // If loading fails, tell the app so that it can try to load again through the proxy.
    // As of chrome 84, stripping the content-security-policy header in `onHeadersReceived` has no effect:
    // requests with the header set to block embedding will error.
    // Don't know if this is a side-effect of a quick security fix or if the behavioural change was intentional.
    // Hopefully it's the former and a future version of chrome will restore the ability to block the header.
    chrome.webNavigation.onErrorOccurred.addListener((details: chrome.webNavigation.WebNavigationFramedErrorCallbackDetails): void => {
        const isProjectIframe: boolean = urlService.isExtensionInitUrl(details.url);
        // If loading fails because request is cancelled and it's extension init url it's not a loading error
        // as we're cancelling it ourselves on purpose
        const wasRequestCanceled: boolean = details.error === "net::ERR_BLOCKED_BY_CLIENT";
        if ((ProjectFrameService.isTopFrame(details.tabId, details.frameId) || isProjectIframe) && !wasRequestCanceled) {
            ProjectFrameService.removeAllForTab(details.tabId);
            void sendTabMessage(
                details.tabId,
                {
                    type: MessageFromExtensionBackgroundScriptType.FRAME_LOAD_ERROR,
                    url: details.url
                },
                {
                    frameId: (details as any).parentFrameId
                }
            );
        }
    });

    // Once the page loads, add the iframe content script that'll inject our bundle into the page
    chrome.webNavigation.onCommitted.addListener((details: chrome.webNavigation.WebNavigationFramedCallbackDetails): void => {
        // Only inject in to MarkUp nested frames
        if (!ProjectFrameService.exists(details.tabId, details.frameId)) {
            return;
        }

        // Ignore blank frames
        if (details.url === "about:blank") {
            return;
        }

        chrome.tabs.executeScript(details.tabId, {
            frameId: details.frameId,
            file: "./project-frame-content.js",
            runAt: "document_start",
            matchAboutBlank: false
        });

        if (ProjectFrameService.isCerosExperience(details.tabId, details.frameId)) {
            chrome.tabs.executeScript(details.tabId, {
                frameId: details.frameId,
                file: "./ceros-experience-content.js",
                runAt: "document_start",
                matchAboutBlank: false
            });
        }
    });

    // Unregister tab/frame when tab is closed
    chrome.tabs.onRemoved.addListener((tabId: number, details: chrome.tabs.TabRemoveInfo): void => {
        ProjectFrameService.removeAllForTab(tabId);
        ProjectCookieStoreService.removeTabId(tabId);
    });
}
