import isObject from "lodash/isObject";
import {fireAndForget} from "@ceros/markup-common/shared/util/promise-utils";
import {Logger} from "@ceros/markup-common/frontend/logger";
import {
    MessageToExtensionBackgroundScriptType,
    isMessageToExtensionBackgroundScript
} from "@ceros/markup-common/frontend/extension/messages/extension-background-script-message";
import {getActiveTab, captureVisibleTab} from "../shared/chrome-api-promises";
import {createProject} from "./background-app";
import {isTabImportable} from "./importable-tabs";
import {UserInfoStorage} from "../shared/storage/user-info-storage";
import {ProjectOpenStorage} from "../shared/storage/project-open-storage";
import {DeprecationStorage} from "../shared/storage/deprecation-storage";
import {ProjectCookieStoreService} from "./project-cookie-stores-service";
import {ProjectFrameService} from "./project-frame-service";
import CookieStoreService from "./cookie-store-service";
import {IProjectData} from "../shared/model/project-frames";

export function listenForMessages(): void {
    chrome.runtime.onMessage.addListener((message: unknown, sender: chrome.runtime.MessageSender, respond: (response?: any) => void): boolean => {
        if (!isMessageToExtensionBackgroundScript(message)) {
            if (process.env.ENV_TYPE !== "local" && isObject(message) && (message as any).type === "SIGN_CONNECT") {
                // keep messages from webpack extension reloader from logging a million errors
                Logger.warn("Background script received invalid message", message);
            }
            return false;
        }

        switch (message.type) {
            case MessageToExtensionBackgroundScriptType.USER_INFO:
                fireAndForget(
                    UserInfoStorage.set(message.userInfo).then((): void => respond(null)),
                    Logger
                );
                return true;

            case MessageToExtensionBackgroundScriptType.PROJECT_OPENS:
                fireAndForget(
                    ProjectOpenStorage.set(message.projectOpens).then((): void => respond(null)),
                    Logger
                );
                return true;

            case MessageToExtensionBackgroundScriptType.POPUP_CREATE_PROJECT:
                fireAndForget(
                    (async (): Promise<void> => {
                        // grab the url right away in case the user switches tabs or navigates
                        const url: string | undefined = (await getActiveTab()).url;

                        if (!url) {
                            // shouldn't happen: popup shouldn't have triggered project creation if there's no url
                            respond(false);
                            return;
                        }

                        respond(true);

                        await createProject(url);
                    })(),
                    Logger
                );
                return true;

            case MessageToExtensionBackgroundScriptType.POPUP_CLEAR_COOKIES:
                fireAndForget(
                    ProjectCookieStoreService.clearCookiesForActiveTab().then((succeeded: boolean): void => {
                        respond(succeeded);
                    }),
                    Logger
                );
                return true;

            case MessageToExtensionBackgroundScriptType.IS_TAB_IMPORTABLE:
                fireAndForget(
                    (async (): Promise<void> => {
                        const tab: chrome.tabs.Tab = await getActiveTab();
                        respond(tab.id && isTabImportable(tab.id));
                    })(),
                    Logger
                );
                return true;

            case MessageToExtensionBackgroundScriptType.EXTENSION_DEPRECATED:
                fireAndForget(
                    DeprecationStorage.set(message.warning).then((): void => respond(null)),
                    Logger
                );
                return true;

            case MessageToExtensionBackgroundScriptType.TAKE_SCREENSHOT:
                fireAndForget(captureVisibleTab().then(respond), Logger);
                return true;
            case MessageToExtensionBackgroundScriptType.SET_COOKIES:
                if (sender.tab && sender.tab.id && sender.frameId) {
                    const projectData: IProjectData | undefined = ProjectFrameService.getProjectData(sender.tab.id, sender.frameId);
                    if (projectData) {
                        const cookieStorage: CookieStoreService = ProjectCookieStoreService.createOrGet(projectData.projectId, sender.tab.id);
                        fireAndForget(
                            cookieStorage.set(message.url, message.cookie, true).then((): void => respond(null)),
                            Logger
                        );
                    }
                }
                return true;
        }
        return false;
    });
}
