const importableTabs: {[tabId: number]: boolean} = {};

export function isTabImportable(tabId: number): boolean {
    // allow tabs that were loaded before the extension was installed
    return !importableTabs.hasOwnProperty(tabId) || importableTabs[tabId];
}

export function listenForImportableTabs(): void {
    chrome.webRequest.onHeadersReceived.addListener(
        (details: chrome.webRequest.WebResponseHeadersDetails): void => {
            const contentTypeHeader: chrome.webRequest.HttpHeader | undefined = (details.responseHeaders || []).find(
                (header: chrome.webRequest.HttpHeader): boolean => header.name.toLowerCase() === "content-type"
            );

            // be loose about the match in case of stuff like `application/xml+xhtml`
            importableTabs[details.tabId] = !contentTypeHeader || !contentTypeHeader.value || /html/.test(contentTypeHeader.value);
        },
        {
            urls: ["<all_urls>"],
            types: ["main_frame"]
        },
        ["responseHeaders"]
    );

    chrome.tabs.onRemoved.addListener((tabId: number, details: chrome.tabs.TabRemoveInfo): void => {
        delete importableTabs[tabId];
    });
}
