import {MessageFromExtensionBackgroundScriptType} from "@ceros/markup-common/frontend/extension/messages/extension-background-script-message";
import {asyncForEach} from "@ceros/markup-common/shared/util/promise-utils";
import {getActiveTab, sendTabMessage} from "../shared/chrome-api-promises";
import {IFrameInfo, IProjectData, IProjectFrames} from "../shared/model/project-frames";
import CookieStoreService from "./cookie-store-service";
import {ProjectFrameService} from "./project-frame-service";

interface IProjectCookieStoreMap {
    [projectId: string]: CookieStoreService;
}
class ProjectCookieStoreServiceConstructor {
    protected projectCookieStores: IProjectCookieStoreMap = {};

    public createOrGet(projectId: string, tabId: number): CookieStoreService {
        if (!this.projectCookieStores[projectId]) {
            this.projectCookieStores[projectId] = new CookieStoreService(tabId, projectId);
        } else {
            this.projectCookieStores[projectId].addTabId(tabId);
        }
        return this.projectCookieStores[projectId];
    }

    public removeTabId(targetTabId: number): void {
        Object.entries(this.projectCookieStores).forEach(([projectId, projectCookieStore]: [string, CookieStoreService]) => {
            if (projectCookieStore.hasTabId(targetTabId)) {
                const wasLastTab: boolean = projectCookieStore.removeTabId(targetTabId);
                if (wasLastTab) {
                    delete this.projectCookieStores[projectId];
                }
            }
        });
    }

    public async clearCookiesForActiveTab(): Promise<boolean> {
        const activeTab: chrome.tabs.Tab | undefined = await getActiveTab();
        const frames: IProjectFrames = ProjectFrameService.getData();
        // if there is active tab and it contains project
        if (activeTab && activeTab.id && frames[activeTab.id]) {
            const framesEntries: Array<[string, IFrameInfo]> = Object.entries(frames[activeTab.id!]);
            const [projectId, parentFrameId]: [string | undefined, number | undefined] = this.findFrameAndProjectIdForTabId(framesEntries);

            if (parentFrameId === undefined || !projectId) {
                return false;
            }
            const cookieStoreService: CookieStoreService = this.createOrGet(projectId!, activeTab.id!);
            // clear cookies for each frame, including nested ones
            await asyncForEach(framesEntries, async (entry: [string, IFrameInfo]): Promise<void> => {
                const projectData: IProjectData | undefined = entry[1].projectData;
                if (projectData) {
                    await cookieStoreService.clear(projectData.projectUrl);
                }
            });
            // send message that extension is busy to reload iframe
            await sendTabMessage(
                activeTab.id!,
                {
                    type: MessageFromExtensionBackgroundScriptType.EXTENSION_BUSY
                },
                {
                    frameId: parentFrameId
                }
            );
            // the frame-load-handler should handle setting it back to ready when the cookie store is setup again
            return true;
        } else {
            return false;
        }
    }

    private findFrameAndProjectIdForTabId(framesEntries: Array<[string, IFrameInfo]>): [string | undefined, number | undefined] {
        // find main frame id of project page and project id
        let parentFrameId: number | undefined;
        let projectId: string | undefined;
        // go through all frames for target tab until we find main project frame
        // one that isn't nested and has parentFrameId (so is still iframe)
        framesEntries.some((entry: [string, IFrameInfo]) => {
            const projectDataForFrame: IProjectData | undefined = entry[1].projectData;
            if (projectDataForFrame) {
                const isParentFrameIdDefined: boolean =
                    typeof projectDataForFrame.parentFrameId === "number" && projectDataForFrame.parentFrameId !== -1;
                if (isParentFrameIdDefined && !entry[1].isNested) {
                    parentFrameId = projectDataForFrame.parentFrameId;
                    projectId = projectDataForFrame.projectId;
                    return true;
                }
            }
            return false;
        });

        return [projectId, parentFrameId];
    }
}

export const ProjectCookieStoreService: ProjectCookieStoreServiceConstructor = new ProjectCookieStoreServiceConstructor();
