import {Logger} from "@ceros/markup-common/frontend/logger";
import {IStashedState} from "../shared/model/stashed-state";
import {StashedStateStorage} from "../shared/storage/stashed-state-storage";
import {LastReloadStorage} from "../shared/storage/last-reload-storage";
import {ProjectFrameService} from "./project-frame-service";

//
// Sometimes the background page stops being able to communicate with the embedded markup.
// When this happens we try reloading the background page.  Hopefully that fixes it.
//
// When reloading we need to
// 1. stash the list of tabs that have markup projects in them and resotre them
// 2. stash the project being created (if any) and retry the project create
//

// if extension reloads more than 6 seconds after we stashed the state, assume something went wonky and start up normally
const STASHED_STATE_TTL: number = 6000;

const RELOAD_THROTTLE_INTERVAL: number = 6 * 60 * 60 * 1000; // 6 hours

export async function reload(creatingProjectUrl?: string): Promise<void> {
    Logger.error("Reloading extension due to markup embed being unresponsive");

    await StashedStateStorage.set({
        projectFrames: ProjectFrameService.getData(),
        creatingProjectUrl,
        stashedAt: Date.now()
    });

    await LastReloadStorage.set(Date.now());

    window.location.reload();
}

export async function restoreStateIfWasReloaded(): Promise<IStashedState | null> {
    const stashedState: IStashedState | null = await StashedStateStorage.get();

    if (stashedState) {
        await StashedStateStorage.setDefault();
        const stashedStateAge: number = Date.now() - stashedState.stashedAt;
        if (stashedStateAge > STASHED_STATE_TTL) {
            Logger.warn(`Stashed state was stale (${stashedStateAge}ms) after reload`);
            return null;
        }

        await ProjectFrameService.setData(stashedState.projectFrames);

        return stashedState;
    }

    return null;
}

export async function hasReloadedTooRecently(): Promise<boolean> {
    const lastReload: number | null = await LastReloadStorage.get();
    return !!lastReload && lastReload + RELOAD_THROTTLE_INTERVAL > Date.now();
}
