import {BrowserExtensionUrlService} from "@ceros/markup-common/frontend/extension/extension-project-iframe-url-service";
import {MessageFromExtensionBackgroundScriptType} from "@ceros/markup-common/frontend/extension/messages/extension-background-script-message";
import {Logger} from "@ceros/markup-common/frontend/logger";
import {fireAndForget} from "@ceros/markup-common/shared/util/promise-utils";
import {sendTabMessage} from "../../shared/chrome-api-promises";
import CookieStoreService from "../cookie-store-service";
import {ProjectCookieStoreService} from "../project-cookie-stores-service";
import {ProjectFrameService} from "../project-frame-service";
import {CurrentReleaseHashStorage} from "../../shared/storage/current-release-hash-storage";

const urlService: BrowserExtensionUrlService = new BrowserExtensionUrlService(chrome.runtime.id);

function pre_1_0_7_handler(details: chrome.webRequest.WebRequestBodyDetails): chrome.webRequest.BlockingResponse {
    ProjectFrameService.add(details.tabId, details.frameId, false);
    return {redirectUrl: urlService.stripParamFromUrl(details.url)};
}

function post_1_0_7_handler(details: chrome.webRequest.WebRequestBodyDetails): chrome.webRequest.BlockingResponse {
    ProjectFrameService.add(details.tabId, details.frameId, false);
    const projectId: string | null = urlService.getProjectId(details.url);
    const projectUrl: string | null = urlService.getProjectUrl(details.url);
    if (projectId && projectUrl) {
        const parsedUrl: URL = new URL(projectUrl);
        ProjectFrameService.setProjectData(details.tabId, details.frameId, {
            projectId,
            projectUrl: parsedUrl.host,
            parentFrameId: details.parentFrameId
        });
        const cookieStorageService: CookieStoreService = ProjectCookieStoreService.createOrGet(projectId, details.tabId);
        fireAndForget(
            (async (): Promise<void> => {
                await cookieStorageService.syncFromChromeToMemoryStorage(projectUrl);
                await sendTabMessage(
                    details.tabId,
                    {
                        type: MessageFromExtensionBackgroundScriptType.EXTENSION_READY
                    },
                    {
                        frameId: details.parentFrameId
                    }
                );
            })(),
            Logger
        );
    }
    return {cancel: true};
}

export function onBeforeRequestHandler(details: chrome.webRequest.WebRequestBodyDetails): chrome.webRequest.BlockingResponse {
    const isNestedInProjectIframe: boolean = ProjectFrameService.exists(details.tabId, details.parentFrameId);
    if (isNestedInProjectIframe) {
        ProjectFrameService.add(details.tabId, details.frameId, isNestedInProjectIframe);
    }
    const isExtensionInit: boolean = urlService.isExtensionInitUrl(details.url);
    const hasExtensionParam: boolean = urlService.urlHasExtensionParam(details.url);

    // keep track of release hash so that we can serve the corresponding version of the proxy-bundle
    const releaseHash: string | null = urlService.getReleaseHash(details.url);
    if (releaseHash) {
        fireAndForget(CurrentReleaseHashStorage.set(releaseHash), Logger);
    }

    // backwards compatibility
    if (hasExtensionParam && !isExtensionInit) {
        return pre_1_0_7_handler(details);
    }

    // post-v1.0.7
    if (isExtensionInit) {
        return post_1_0_7_handler(details);
    }

    return {};
}
