import {IExtensionUserInfo} from "@ceros/markup-common/frontend/extension/extension-user-info";
import {IExtensionDeprecationWarning} from "@ceros/markup-common/frontend/extension/extension-deprecation-warning";
import {UserInfoStorage} from "../shared/storage/user-info-storage";
import {DeprecationStorage} from "../shared/storage/deprecation-storage";

const LOGGED_IN_ICON: string = "assets/logo-48.png";
const LOGGED_OUT_ICON: string = "assets/logo-grey-48.png";
const DEPRECATED_ICON: string = "assets/logo-deprecated.png";

export function initIconAndBadge(): void {
    setIcon(false); // assume initially logged out
    setEnvironmentBadge();

    UserInfoStorage.onUpdate((userInfo: IExtensionUserInfo): void => {
        setIcon(!!userInfo);
    });

    DeprecationStorage.onUpdate((deprecationWarning: IExtensionDeprecationWarning): void => {
        if (deprecationWarning) {
            chrome.browserAction.setIcon({
                path: DEPRECATED_ICON
            });
        }
    });
}

function setIcon(loggedIn: boolean): void {
    chrome.browserAction.setIcon({
        path: loggedIn ? LOGGED_IN_ICON : LOGGED_OUT_ICON
    });
}

function setEnvironmentBadge(): void {
    if (process.env.ENV_TYPE !== "prod") {
        chrome.browserAction.setBadgeBackgroundColor({color: "#777"});
        chrome.browserAction.setBadgeText({text: process.env.ENV_NAME!.substr(0, 3)});
    }
}
