import {AbstractStorage} from "./abstract-storage";

class CurrentReleaseHashStorageConstructor extends AbstractStorage<string> {
    constructor() {
        super();
        this.storageKey = "currentReleaseHash";
        this.defaultValue = process.env.MARKUP_RELEASE_HASH!;
    }
}

export const CurrentReleaseHashStorage: CurrentReleaseHashStorageConstructor = new CurrentReleaseHashStorageConstructor();
