import {fireAndForget} from "@ceros/markup-common/shared/util/promise-utils";
import {Logger} from "@ceros/markup-common/frontend/logger";
import {IProjectOpen} from "@ceros/markup-common/frontend/project-open";
import {IExtensionUserInfo} from "@ceros/markup-common/frontend/extension/extension-user-info";
import {AbstractStorage} from "./abstract-storage";
import {UserInfoStorage} from "./user-info-storage";

class ProjectOpenStorageConstructor extends AbstractStorage<IProjectOpen[]> {
    constructor() {
        super();
        this.storageKey = "projectOpens";
        this.defaultValue = [];

        UserInfoStorage.onUpdate((value: IExtensionUserInfo): void => {
            if (!value) {
                fireAndForget(this.set([]), Logger);
            }
        });
    }
}

export const ProjectOpenStorage: ProjectOpenStorageConstructor = new ProjectOpenStorageConstructor();
