import {APP_ORIGIN} from "../shared/app-origin";
import {tabQuery} from "../shared/chrome-api-promises";

// The content script declaration in the manifest will make chrome add our content script to new pages.
// It won't when the extension is reloaded, so we do that here so that existing instances of markup can
// continue sending project-open update and logout notifications.
export async function injectContentScriptIntoMarkupTabs(): Promise<void> {
    (await tabQuery({url: `${APP_ORIGIN}/*`})).forEach((tab: chrome.tabs.Tab): void => {
        if (tab.id) {
            chrome.tabs.executeScript(tab.id, {
                file: "markup-content.js",
                runAt: "document_start"
            });
        }
    });
}
