import {IProjectFrames, IProjectData} from "../shared/model/project-frames";
import {tabQuery} from "../shared/chrome-api-promises";
import findKey from "lodash/findKey";

// Keep list of all tabs that were loaded into iframe for markup project.
// These chill in memory which should be fine: they'll get wiped out when the browser is closed but then so will the tabs.
interface IProjectFrameService {
    add: (tabId: number, frameId: number, isNested: boolean) => void;
    exists: (tabId: number, frameId: number) => boolean;
    isTopFrame: (tabId: number, frameId: number) => boolean;
    removeAllForTab: (tabId: number) => void;
    markCerosExperience: (tabId: number, frameId: number) => void;
    unmarkCerosExperience: (tabId: number, frameId: number) => void;
    isCerosExperience: (tabId: number, frameId: number) => boolean;
    setProjectData: (tabId: number, frameId: number, updateData: IProjectData) => void;
    getProjectData: (tabId: number, frameId: number) => IProjectData | undefined;
    getData: () => IProjectFrames;
    setData: (value: IProjectFrames) => void;
    getTopFrameId: (tabId: number) => number | undefined;
}
export const ProjectFrameService: IProjectFrameService = ((): IProjectFrameService => {
    let data: IProjectFrames = {};

    return {
        add: (tabId: number, frameId: number, isNested: boolean): void => {
            if (!data[tabId]) {
                data[tabId] = {};
            }
            if (!data[tabId][frameId]) {
                data[tabId][frameId] = {isCerosExperience: false, isNested};
            }
        },
        exists: (tabId: number, frameId: number): boolean => {
            return !!data[tabId] && !!data[tabId][frameId];
        },
        isTopFrame: (tabId: number, frameId: number): boolean => {
            return !!data[tabId] && !!data[tabId][frameId] && !data[tabId][frameId].isNested;
        },
        removeAllForTab: (tabId: number): void => {
            delete data[tabId];
        },
        markCerosExperience: (tabId: number, frameId: number): void => {
            data[tabId][frameId].isCerosExperience = true;
        },
        unmarkCerosExperience: (tabId: number, frameId: number): void => {
            data[tabId][frameId].isCerosExperience = false;
        },
        setProjectData: (tabId: number, frameId: number, projectData: IProjectData): void => {
            if (data[tabId][frameId]) {
                data[tabId][frameId].projectData = projectData;
            }
        },
        getProjectData: (tabId: number, frameId: number): IProjectData | undefined => {
            return data[tabId][frameId].projectData;
        },
        isCerosExperience: (tabId: number, frameId: number): boolean => {
            return data[tabId][frameId].isCerosExperience;
        },
        getData: (): IProjectFrames => data,
        setData: async (frames: IProjectFrames): Promise<void> => {
            const tabIds: number[] = (await tabQuery({}))
                .map((tab: chrome.tabs.Tab): number | undefined => tab.id)
                .filter((id: number | undefined): boolean => id !== undefined) as number[];

            for (const tabId in frames) {
                if (!tabIds.includes(parseInt(tabId, 10))) {
                    delete frames[tabId];
                }
            }
            data = frames;
        },
        getTopFrameId: (tabId: number): number | undefined => {
            const topTab: string | undefined = findKey(data[tabId], {isNested: false});
            if (topTab) {
                return parseInt(topTab, 10);
            }
        }
    };
})();
