import {IFRAME_HEADER_BLACKLIST} from "@ceros/markup-common/shared/model/iframe-header-blacklist";
import {MARKUP_REQUEST_HEADER} from "@ceros/markup-common/shared/model/markup-request-header";
import {MARKUP_USER_AGENT_HEADER} from "@ceros/markup-common/shared/model/markup-user-agent-header";
import {IProjectData} from "../../shared/model/project-frames";
import {isCerosExperienceResponse} from "../ceros-experience-detector";
import CookieStoreService from "../cookie-store-service";
import {ProjectCookieStoreService} from "../project-cookie-stores-service";
import {ProjectFrameService} from "../project-frame-service";
import {hasHeader} from "../util";

type chromeHeader = chrome.webRequest.HttpHeader;

export function onBeforeSendHeadersHandler(details: chrome.webRequest.WebRequestHeadersDetails): chrome.webRequest.BlockingResponse | void {
    if (ProjectFrameService.exists(details.tabId, details.frameId)) {
        const projectData: IProjectData | undefined = ProjectFrameService.getProjectData(details.tabId, details.frameId);
        if (!projectData) {
            return {};
        }

        const cookieStoreService: CookieStoreService = ProjectCookieStoreService.createOrGet(projectData.projectId, details.tabId);

        if (!details.requestHeaders) {
            details.requestHeaders = [];
        }

        if (!hasHeader("cookie", details.requestHeaders)) {
            details.requestHeaders.push({name: "cookie", value: ""});
        }

        // append user agent
        const requestHeaders: chromeHeader[] = (details.requestHeaders || []).map((header: chromeHeader): chromeHeader => {
            switch (header.name.toLowerCase()) {
                case "user-agent":
                    return {
                        name: header.name,
                        value: `${header.value} ${MARKUP_USER_AGENT_HEADER} MarkUpExtension/${process.env.EXTENSION_VERSION}`
                    };
                case "cookie":
                    const cookieHeader: chromeHeader | undefined = cookieStoreService.handleRequestHeader(header, details.url);
                    if (cookieHeader) {
                        return cookieHeader;
                    }
                    return header;
                default:
                    return header;
            }
        });

        // add our custom header to iframe requests only, not on other subrequests so that they don't get blocked
        // due to an `access-control-allow-headers` header on the cors options response that doesn't include our header
        if (details.type === "sub_frame") {
            requestHeaders.push(MARKUP_REQUEST_HEADER);
        }

        return {requestHeaders};
    }
}

export function onHeadersReceivedHandler(details: chrome.webRequest.WebResponseHeadersDetails): chrome.webRequest.BlockingResponse | void {
    if (ProjectFrameService.exists(details.tabId, details.frameId)) {
        // check for ceros experience now that we have response headers
        if (isCerosExperienceResponse(details) && details.type === "sub_frame") {
            ProjectFrameService.markCerosExperience(details.tabId, details.frameId);
        }

        // overwrite response headers with new list with blacklisted headers excluded
        return {
            responseHeaders: details
                .responseHeaders!.filter((header: chromeHeader) => !IFRAME_HEADER_BLACKLIST.includes(header.name.toLowerCase()))
                .map((header: chromeHeader): chromeHeader => {
                    const projectData: IProjectData | undefined = ProjectFrameService.getProjectData(details.tabId, details.frameId);
                    if (projectData) {
                        const cookieStoreService: CookieStoreService = ProjectCookieStoreService.createOrGet(projectData.projectId, details.tabId);
                        cookieStoreService.handleResponseHeader(header, details.url);
                    }
                    return header;
                })
        };
    }
}
