import {CookieJar, Cookie, MemoryCookieStore} from "tough-cookie";
import {getObfuscationTLD, getObfuscatedUrl, getObfuscatedDomain, getUnobfuscatedUrl} from "@ceros/markup-common/shared/util/url-util";
import {MessageFromExtensionToIframeBackgroundScriptType} from "@ceros/markup-common/frontend/extension/messages/extension-iframe-message";
import {Logger} from "@ceros/markup-common/frontend/logger";
import {getAllChromeCookies, removeChromeCookie, sendTabMessage, setChromeCookie} from "../shared/chrome-api-promises";
import {asyncForEach, fireAndForget} from "@ceros/markup-common/shared/util/promise-utils";
import {ProjectFrameService} from "./project-frame-service";
const SAME_SITE_MAP: Array<[string, chrome.cookies.SameSiteStatus]> = [
    ["none", "no_restriction"],
    ["lax", "lax"],
    ["strict", "strict"]
];

type commonProperties = keyof Cookie.Properties & keyof chrome.cookies.Cookie;

/**
 * this service sets cookies in multiple stores
 * cookieJar is memory cookie store - it allows to overwrite request cookie headers
 * window.markupCookieJar is memory cookie store in project iframe - it allows to overwrite document.cookie getter
 * it also sets cookie in chrome cookie store under obfuscated url, so we keep them when user closes browser
 */
export default class CookieStoreService {
    private tabIds: Set<number>;
    private projectId: string;
    private cookieJar: CookieJar;
    public constructor(tabId: number, projectId: string) {
        this.cookieJar = new CookieJar(new MemoryCookieStore(), {rejectPublicSuffixes: false});
        this.tabIds = new Set([tabId]);
        this.projectId = projectId;
    }

    /**
     * Sets cookies both in Chrome cookie store and in memory store
     * @param url
     * @param cookie
     */
    public async set(url: string, cookie: string | Cookie, saveToStore: boolean = true): Promise<void> {
        let parsedCookie: Cookie | undefined;
        if (typeof cookie === "string") {
            try {
                parsedCookie = Cookie.parse(cookie);
            } catch (err: any) {
                Logger.error(err);
            }
        } else {
            parsedCookie = cookie;
        }
        if (parsedCookie) {
            this.setCookieInIframe(parsedCookie);
            if (parsedCookie.domain) {
                parsedCookie.domain = this.getObfuscatedDomain(parsedCookie.domain);
            }
            const obfuscatedUrl: string = this.getObfuscatedUrl(url);
            await this.cookieJar.setCookie(parsedCookie!, obfuscatedUrl);
            if (saveToStore) {
                await setChromeCookie({url: obfuscatedUrl, ...this.convertCookieToChromeCookie(parsedCookie)});
            }
        }
    }

    /**
     * Read all the cookies kept in Chrome cookie store for target url and add them to memory stores
     * @param url - unobfuscated url of target page
     */
    public async syncFromChromeToMemoryStorage(url: string): Promise<void> {
        const chromeCookies: chrome.cookies.Cookie[] = await this.getProjectChromeCookies();
        const parsedCookies: Cookie[] = chromeCookies.map((chromeCookie: chrome.cookies.Cookie): Cookie => {
            return this.convertChromeCookieToCookie(chromeCookie);
        });
        await this.setCookiesArray(parsedCookies, url);
    }

    public async clear(url: string): Promise<void> {
        this.cookieJar.removeAllCookiesSync();
        const chromeCookies: chrome.cookies.Cookie[] = await this.getProjectChromeCookies();
        await asyncForEach(chromeCookies, async (cookie: chrome.cookies.Cookie): Promise<void> => {
            await removeChromeCookie({url: this.getObfuscatedUrl(url), name: cookie.name});
        });
    }

    public handleRequestHeader(header: chrome.webRequest.HttpHeader, url: string): chrome.webRequest.HttpHeader | undefined {
        if (header.name.toLowerCase() === "cookie") {
            const cookiesFromMem: Cookie[] = this.get(this.getObfuscatedUrl(url));
            const cookieStrings: string[] = cookiesFromMem.map((memCookie: Cookie) => {
                return `${memCookie.key}=${memCookie.value}`;
            });
            header.value = cookieStrings.join("; ");
        }
        return header.value ? header : undefined;
    }

    public handleResponseHeader(header: chrome.webRequest.HttpHeader, url: string): void {
        if (header.name.toLowerCase() === "set-cookie" && header.value) {
            fireAndForget(this.set(url, header.value), Logger);
        }
    }

    public addTabId(tabId: number): void {
        this.tabIds.add(tabId);
    }

    /**
     * @returns boolean indicating it was a last tab (true) or not (false)
     */
    public removeTabId(tabId: number): boolean {
        this.tabIds.delete(tabId);
        return this.tabIds.size === 0;
    }

    public hasTabId(tabId: number): boolean {
        return this.tabIds.has(tabId);
    }

    /**
     * Gets cookies synchronously from extension memory store
     * @param url
     */
    private get(url: string): Cookie[] {
        return this.cookieJar.getCookiesSync(url);
    }

    private async getProjectChromeCookies(): Promise<chrome.cookies.Cookie[]> {
        const cookies: chrome.cookies.Cookie[] = await getAllChromeCookies();
        return cookies.filter((chromeCookie: chrome.cookies.Cookie): boolean => {
            return chromeCookie.domain.includes(getObfuscationTLD(this.projectId));
        });
    }

    private getObfuscatedUrl(url: string): string {
        return getObfuscatedUrl(url, this.projectId);
    }

    private getObfuscatedDomain(domain: string): string {
        return getObfuscatedDomain(domain, this.projectId);
    }

    private setCookiesArray(cookies: Cookie[], url: string): Promise<unknown[]> {
        return Promise.all(
            cookies.map((cookie: Cookie): Promise<unknown> => {
                return this.set(url, cookie, false).catch((err: any) => Logger.error(err));
            })
        );
    }

    private setCookieInIframe(cookie: Cookie): void {
        this.tabIds.forEach((tabId: number) => {
            const frameId: number | undefined = ProjectFrameService.getTopFrameId(tabId);
            if (frameId) {
                const clonedCookie: Cookie = cookie.clone();
                if (clonedCookie.domain) {
                    clonedCookie.domain = getUnobfuscatedUrl(clonedCookie.domain);
                }
                fireAndForget(
                    sendTabMessage(
                        tabId,
                        {
                            type: MessageFromExtensionToIframeBackgroundScriptType.SET_COOKIE,
                            cookie: clonedCookie.cookieString()
                        },
                        {
                            frameId
                        }
                    ),
                    Logger
                );
            }
        });
    }

    private convertCookieToChromeCookie(cookie: Cookie): Partial<chrome.cookies.SetDetails | undefined> {
        const sameSiteMatch: [string, chrome.cookies.SameSiteStatus] | undefined = SAME_SITE_MAP.find(
            (pair: [string, chrome.cookies.SameSiteStatus]) => pair[0] === cookie.sameSite
        );
        const sameSiteStatus: chrome.cookies.SameSiteStatus = sameSiteMatch ? sameSiteMatch[1] : "unspecified";
        const expiryTime: number = cookie.expiryTime();
        const isSessionCookie: boolean = !isFinite(expiryTime);
        if (cookie.domain) {
            cookie.domain = this.getObfuscatedDomain(cookie.domain);
        }
        return {
            name: cookie.key,
            value: cookie.value,
            expirationDate: isSessionCookie ? undefined : expiryTime,
            domain: cookie.domain ? cookie.domain : undefined,
            path: cookie.path ? cookie.path : undefined,
            sameSite: sameSiteStatus,
            secure: sameSiteStatus === "no_restriction" ? true : cookie.secure,
            httpOnly: cookie.httpOnly
        };
    }

    private convertChromeCookieToCookie(cookie: chrome.cookies.Cookie): Cookie {
        const commonArgs: commonProperties[] = ["value", "path", "secure", "httpOnly"];
        const sameSiteMatch: [string, chrome.cookies.SameSiteStatus] | undefined = SAME_SITE_MAP.find(
            (pair: [string, chrome.cookies.SameSiteStatus]) => pair[1] === cookie.sameSite
        );
        const expires: Date | undefined = cookie.expirationDate ? new Date(cookie.expirationDate * 1000) : undefined;
        return new Cookie({
            ...commonArgs.reduce((acc: Partial<Cookie.Properties>, key: commonProperties): Partial<Cookie.Properties> => {
                return {...acc, [key]: cookie[key]};
            }, {}),
            key: cookie.name,
            value: cookie.value ? cookie.value : "",
            expires,
            sameSite: sameSiteMatch ? sameSiteMatch[0] : undefined,
            domain: cookie.domain
        });
    }
}
