import {fireAndForget} from "@ceros/markup-common/shared/util/promise-utils";
import {Logger} from "@ceros/markup-common/frontend/logger";
import {requestInfo} from "./background-app";
import {UserInfoStorage} from "../shared/storage/user-info-storage";
import {ProjectOpenStorage} from "../shared/storage/project-open-storage";
import {DeprecationStorage} from "../shared/storage/deprecation-storage";
import {clearAlarm} from "../shared/chrome-api-promises";

const REFRESH_ALARM_NAME: string = "refresh";
const REFRESH_INTERVAL_MINUTES: number = 5;

export function initRefresher(): void {
    UserInfoStorage.onUpdate(scheduleRefresh);
    ProjectOpenStorage.onUpdate(scheduleRefresh);
    fireAndForget(scheduleRefresh(), Logger);
}

async function scheduleRefresh(): Promise<void> {
    const extensionIsDeprecated: boolean = !!(await DeprecationStorage.get());
    if (!extensionIsDeprecated) {
        await clearAlarm(REFRESH_ALARM_NAME);
        chrome.alarms.create(REFRESH_ALARM_NAME, {delayInMinutes: REFRESH_INTERVAL_MINUTES});
    }
}

chrome.alarms.onAlarm.addListener((alarm: chrome.alarms.Alarm): void => {
    if (alarm.name === REFRESH_ALARM_NAME) {
        fireAndForget(requestInfo(), Logger);
        // refresh will be rescheduled once data comes back but do it here just in case the app doesn't respond for whatever reason
        fireAndForget(scheduleRefresh(), Logger);
    }
});
