import {EventEmitter} from "events";

const eventEmitter: EventEmitter = new EventEmitter();

export abstract class AbstractStorage<T> extends EventEmitter {
    protected storageKey: string;
    protected defaultValue: T;

    public get(): Promise<T> {
        return new Promise((resolve: (value: T) => void, reject: (error: Error) => void): void => {
            chrome.storage.local.get([this.storageKey], (result: {[key: string]: any}): void => {
                resolve(result[this.storageKey] ?? this.defaultValue);
            });
        });
    }

    public set(value: T): Promise<void> {
        return new Promise((resolve: () => void, reject: (error: Error) => void): void => {
            const obj: {[key: string]: T} = {};
            obj[this.storageKey] = value;
            chrome.storage.local.set(obj, (): void => {
                this.emit("update", value);
                resolve();
            });
        });
    }

    public setDefault(): Promise<void> {
        return this.set(this.defaultValue);
    }

    public onUpdate(handler: (value: T) => any): void {
        this.on("update", handler);
    }
}
