import {IStashedState} from "../model/stashed-state";
import {AbstractStorage} from "./abstract-storage";

class StashedStateStorageConstructor extends AbstractStorage<IStashedState | null> {
    constructor() {
        super();
        this.storageKey = "stashedState";
        this.defaultValue = null;
    }
}

export const StashedStateStorage: StashedStateStorageConstructor = new StashedStateStorageConstructor();
