import {
    isACerosViewHost,
    isACerosPreviewHost,
    CEROS_EXPERIENCE_IDENTIFYING_HEADER,
    CEROS_EXPERIENCE_VARY_HEADER_MATCH
} from "@ceros/markup-common/shared/util/ceros";

export function isCerosExperienceResponse(details: chrome.webRequest.WebResponseHeadersDetails): boolean {
    const url: URL = new URL(details.url);

    if (isACerosViewHost(url.host) || isACerosPreviewHost(url.host)) {
        return true;
    }

    // kinda flakey but the only distinguishing feature of a published ceros experience on vanity domains.. :-/
    return (details.responseHeaders || []).some((header: chrome.webRequest.HttpHeader): boolean => {
        if (header.name.toLowerCase() === CEROS_EXPERIENCE_IDENTIFYING_HEADER) {
            return true;
        }
        if (header.name.toLowerCase() === "vary") {
            return !!(header.value && header.value.toLowerCase().match(CEROS_EXPERIENCE_VARY_HEADER_MATCH));
        }
        return false;
    });
}
