import {ONE_DAY_IN_MILLISECONDS} from "@ceros/markup-common/shared/util/date-utils";
import {AbstractStorage} from "./abstract-storage";

const PERIOD: number = ONE_DAY_IN_MILLISECONDS;

class IframeLoadFailsStorageConstructor extends AbstractStorage<number[]> {
    constructor() {
        super();
        this.storageKey = "iframeLoadFails";
        this.defaultValue = [];
    }

    public async add(): Promise<number> {
        // don't worry about atomicity: these r/w are fast and it's unlikely a client is going to be registering multiple load failures at once
        const events: number[] = await this.get();
        const now: number = Date.now();
        while (events.length > 0 && events[0] < now - PERIOD) {
            events.shift();
        }
        events.push(now);
        this.set(events);
        return events.length;
    }
}

export const IframeLoadFailsStorage: IframeLoadFailsStorageConstructor = new IframeLoadFailsStorageConstructor();
